import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home.vue'
import results from '../views/results.vue'
import Meta from 'vue-meta'

Vue.use(VueRouter)
Vue.use(Meta)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/results',
    name: 'results',
    component: results
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
