<template>
  <div class="home">
    <p>第2回順位発表の順番</p>
    <ul class="trainee-list">
      <li class="trainee-list_item trainee-btn" v-for="(item, index) in this.$store.state.trainee" v-bind:key="index">
        <input name="choose" class="trainee-checkbox" type="checkbox" v-bind:id="item.id" v-bind:value="item.id" v-on:change="radio($event)">
        <label v-bind:for="item.id" class="trainee-label">
          <img v-bind:src="require('@/assets/photo/' + item.id + '.jpg')" v-bind:alt="item.name" class="trainee-label_img">
          <p class="trainee-label_name" v-html="item.name.replace('・', '・<br>')"></p>
          <p class="trainee-label_rank"></p>
        </label>
      </li>
    </ul>
    <div class="btn-wrap">
      <button v-on:click="allclear" class="btn reset-btn">リセット</button>
      <button v-on:click="capture" class="btn submit-btn">決定！</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  mounted() {
    if(this.$store.state.choose.length != 0) {
      for(let i in this.$store.state.choose) {
        let dischbox = document.querySelector('[value="' + this.$store.state.choose[i] + '"]')
        let rank = dischbox.nextElementSibling.lastElementChild
        rank.innerText = Number(i) + 1
        dischbox.checked = true
      }
      const chbox = document.querySelectorAll('[name="choose"]:checked')
      if(chbox.length > 10) {
        const notchbox = document.querySelectorAll('[name="choose"]:not(:checked)')
        notchbox.forEach(e => {
          e.disabled = true
        })
      } else {
        const dischbox = document.querySelectorAll('[name="choose"]')
        dischbox.forEach(e => {
          e.disabled = false
        })
      }
    }
  },
  methods: {
    radio(e) {
      const chbox = document.querySelectorAll('[name="choose"]:checked')
      let id = e.srcElement.value
      const rank = e.srcElement.nextElementSibling.lastElementChild
      if(e.srcElement.checked && this.$store.state.choose.indexOf('') == -1) {
        this.$store.state.choose.push(id)
        rank.innerText = this.$store.state.choose.length
      } else if(!e.srcElement.checked) {
        let e = this.$store.state.choose.indexOf(id)
        this.$store.state.choose[e] = ''
        rank.innerText = ''
      } else if(e.srcElement.checked && this.$store.state.choose.indexOf('') != -1) {
        let nullindex = this.$store.state.choose.indexOf('')
        this.$store.state.choose[nullindex] = id
        rank.innerText = Number(nullindex) + 1
      }
      if(chbox.length > 10) {
        const notchbox = document.querySelectorAll('[name="choose"]:not(:checked)')
        notchbox.forEach(e => {
          e.disabled = true
        })
      } else {
        const dischbox = document.querySelectorAll('[name="choose"]')
        dischbox.forEach(e => {
          e.disabled = false
        })
      }
    },
    allclear() {
      const dischbox = document.querySelectorAll('[name="choose"]')
      this.$store.state.choose = []
      dischbox.forEach(e => {
        let rank = e.nextElementSibling.lastElementChild
        rank.innerText = ''
        e.disabled = false
        e.checked = false
      })
    },
    capture() {
      const chbox = document.querySelectorAll('[name="choose"]:checked')
      let chooselist = []
      window.scrollTo(0, 0)
      if(chbox.length == 11) {
        for(let i in this.$store.state.choose) {
          chooselist.push(this.$store.state.choose[i])
        }
        const hashtext = chooselist.join('-')
        this.$router.push('results?' + hashtext)
      } else {
        alert('11人えらんで')
      }
    }
  }
}
</script>

