<template>
  <div class="results">
    <div class="results-section" id="capture">
      <ul class="results-list">
        <li v-for="(item, index) in this.traineeArray" v-bind:key="index" class="results-list_item">
          <div class="results-img"><img v-bind:src="require('@/assets/photo/' + item.id + '.jpg')" v-on:load="img(index)"></div>
          <p class="results-name">{{ item.name }}</p>
        </li>
      </ul>
    </div>
    <p>デビューハジャ</p>
    <div id="result"></div>
    <div class="btn-wrap is-static"><router-link tag="a" to="/" class="btn reset-btn is-single">選び直す</router-link></div>
    <div class="btn-wrap is-static">
      <a v-bind:href="'https://twitter.com/intent/tweet?text=%E7%A7%81%E3%81%8C%E9%81%B8%E3%82%93%E3%81%A011%E4%BA%BA%F0%9F%98%8C%0D%0A&url=' + shareURL" class="btn back-btn" target="_blank">ツイート</a>
      <a v-bind:href="'https://social-plugins.line.me/lineit/share?url=' + shareURL" class="btn line-btn" target="_blank">LINE</a>
    </div>
  </div>
</template>

<script>
export default {
  metaInfo: {
    meta: [
      {
        property: 'og:site_name',
        content: 'デビューメンバーは自分が決める'
      },
      {
        property: 'og:type',
        content: 'website'
      },
      {
        property: 'og:url',
        content: 'https://onodo.github.io/' + location.hash
      },
      {
        property: 'og:image',
        content: 'https://onodo.github.io/' + require('@/assets/summary.jpg')
      }
    ]
  },
  data() {
    return {
      shareURL: '',
      traineeArray: [],
      imgArray: []
    }
  },
  methods: {
    img(index) {
      this.imgArray.push(index)
      if(this.imgArray.length == 11) {
        const html2canvas = require('html2canvas')
        const img = document.createElement('img')
        const resultsection = document.querySelector('.results-section')
        const resultHeight = resultsection.clientHeight
        resultsection.setAttribute('style', 'height:' + resultHeight + 'px')
        html2canvas(document.querySelector("#capture")).then(canvas => {
          const re = document.querySelector("#result")
          const src = canvas.toDataURL()
          img.setAttribute('src', src)
          if (re.childNodes.length !== 0) {
            re.textContent = null
            re.appendChild(img)
          } else {
            re.appendChild(img)
          }
        })
      }
    }
  },
  created() {
    if(this.$store.state.choose.length == 0) {
      const hash = location.hash
      this.shareURL = encodeURIComponent(location.href)
      const hashArray = hash.replace('#/results?', '').split('-')
      for (let i in hashArray) {
        for (let j in this.$store.state.trainee) {
          if (hashArray[i] == this.$store.state.trainee[j].id) {
            this.traineeArray.push(this.$store.state.trainee[j])
          }
        }
      }
      return this.traineeArray
    } else {
      for (let j in this.$store.state.choose) {
        for (let i in this.$store.state.trainee) {
          if (this.$store.state.choose[j] == this.$store.state.trainee[i].id) {
            this.traineeArray.push(this.$store.state.trainee[i])
          }
        }
      }
      return this.traineeArray
    }
  },
  mounted() {
    if (this.$store.state.choose.length != 0) {
      const html2canvas = require('html2canvas')
      const img = document.createElement('img')
      const resultsection = document.querySelector('.results-section')
      const resultHeight = resultsection.clientHeight
      resultsection.setAttribute('style', 'height:' + resultHeight + 'px')
      html2canvas(document.querySelector("#capture")).then(canvas => {
        const re = document.querySelector("#result")
        const src = canvas.toDataURL()
        img.setAttribute('src', src)
        if (re.childNodes.length !== 0) {
          re.textContent = null
          re.appendChild(img)
        } else {
          re.appendChild(img)
        }
      })
    }
  }
}
</script>

