  position: absolute;
  right: 0;
  top: -600px;
  z-index: -1;
  width: 320px;
}
.results-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style-type: none;
  margin: 0;
  padding: 0;
  &_item {
    flex: 0 0 23%;
    width: 23%;
    &:first-child {
      flex: 0 0 100%;
      width: 100%;
      .results-item {width: 50%;}
    }
    &:not(:first-child) {margin-top: 15px;}
    &:nth-child(2),
    &:nth-child(3) {
      flex: 0 0 50%;
      width: 50%;
      .results-item {width: 80%;}
    }
  }
}
.results-img {
  background: #3e64ff;
  border: 5px solid #3e64ff;
  border-radius: 50%;
  color: #fff;
  display: block;
  margin: auto;
  overflow: hidden;
  max-width: 120px;
  img {
    display: block;
    max-width: 100%;
  }
}
.results-name {
  font-weight: bold;
  font-size: 14px;
  margin: 5px 0 0 0;
  text-align: center;
}
#result {
  background: #fff;
  text-align: center;
  &:empty {
    padding: 0 10px;
    &::after {
      animation: show .4s ease 3s 1 normal forwards;
      content: '待っても画像が出てこない場合は画面を更新してください';
      color: #cc1e1e;
      display: block;
      opacity: 0;
      padding: 1em 0;
    }
  }
  img {max-width: 340px;}
}
